.experience {
    background-color: #f9f9f9;
    padding: 4rem 2rem;
    border-radius: 12px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    max-width: 1200px;
    margin: 0 auto;
}

.experience h2 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
    color: #333;
    text-align: center;
}

.experience-list {
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.experience-item {
    /* background-color: #fff; */
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.experience-item:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    transform: translateY(-5px);
}

.experience-role {
    font-size: 1.6rem;
    font-weight: 600;
    color: #222;
    margin: 0;
}

.experience-company {
    font-size: 1.2rem;
    color: #555;
    margin: 0.5rem 0;
}

.experience-duration {
    font-size: 1rem;
    color: #777;
    margin: 0;
}

.experience-description {
    font-size: 1rem;
    color: #333;
    margin-top: 1rem;
    line-height: 1.5;
}

/* View Certificate Button Styling */
.view-certificate-btn {
    display: inline-block;
    margin-top: 1rem;
    padding: 10px 20px;
    background-color: #17a589; /* Green background */
    color: #fff; /* White font color */
    border-radius: 5px;
    text-decoration: none;
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.view-certificate-btn:hover {
    background-color: #17a589; /* Darker green on hover */
    transform: translateY(-3px);
    color:#fff;
}
